import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { getDataNextSteps, managePostHeight } from '../../../../actions/themeActions'

class SeeMore extends PureComponent {

   state={
     moreData : 1
  }

  upatemoreData = ()=> {
     this.setState({ moreData : this.state.moreData==2 ? 0 : ++this.state.moreData })
  }

  onLoadeMore = event => {
    const { wall, appendData, postHeight } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    const timeStamp = Math.floor(Date.now() / 1000);

    if(!wall.product) {
       this.props.managePostHeight(postHeight);
    }

    this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent,this.upatemoreData);
  }

  render() {

    const { languageSetting, loaderData, postData, wall } = this.props;


    const hasMoreData = postData.hasMoreData && Object.keys(postData.hasMoreData).length > 0 ? postData.hasMoreData[0] && postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true


    
    return (

      hasMoreData === true && this.state.moreData ?
        <div className="tb_see_more_btn_wrap">
          <div className={`tb_see_more_btn ${(loaderData && loaderData.isShowMoreLoading) ? `tb_more_btn_loading` : ``}`} onClick={this.onLoadeMore} style={{
            backgroundColor : wall.ThemeRule.cardColor,color : wall.ThemeRule.fontColor, borderColor : wall.ThemeRule.cardColor=='#ffffff' ? "#000" : wall.ThemeRule.cardColor }}>{hasMoreData ? languageSetting.buttonName : languageSetting.no_more}</div>
        </div>

        : <></>

    );
  }
}


const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
    postHeight: state.postHeight.data
  }
}

const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data)),
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, upatemoreData) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, upatemoreData))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SeeMore);